<template>
	<form @submit.prevent v-if="answers.length">
		<p class="wording">What's the missing letter? <br>{{wording}}</p>
			<div class="checking">
				<div class="column1" >
					<div v-for="answer in answers" :key="answer">
							<input @onchange="option($event)" class="check" type=radio name="letter" v-model="selected" :value="answer"/>
						<label class="label" for="letter">
							{{answer}}
						</label>
					</div>
				</div>
				<div class="column2">
					<div>
					</div>
				</div>
			</div>
			<p class="msg">{{msg}}</p>
		<button @click="nextquestions()" class="my_answer" @submit.prevent>Answer</button>
	</form>
</template>
<script>

export default {
	name: 'qa',
	props:[
			'word',
			'counter',
			'questionorder'
	],
	data(){
		return{
			"answer1":'',
			"answer2":'',
			"answer3":'',
			"missingletters": '',
			"missingletter": '',
			"length": 0,
			"wording": '',
			"alphabet": [],
			"answers": [],
			"selected": '',
			"msg": '',
			
		}
	},
	created: function(){
		this.length = this.word.length
		while(this.missingletter <=0){
			this.missingletter = Math.floor((Math.random() * this.length))
		}
		this.wording = this.word.substring(0, (this.missingletter-1))+"_"+this.word.substring(this.missingletter,this.length)
		this.missingletter = this.word.substr((this.missingletter-1), 1)
		
		this.alphabet = ['a','b','c','d','e','f','g','h','i','j','k','l','m','n','o','p','q','r','s','t','u','v','w','x','y','z'];
		this.alphabet = this.removing(this.alphabet, this.missingletter)
		this.answer1 = this.alphabet[Math.floor(Math.random() * this.alphabet.length)];
		this.alphabet = this.removing(this.alphabet, this.answer1)
		this.answer2 = this.alphabet[Math.floor(Math.random() * this.alphabet.length)];
		this.alphabet = this.removing(this.alphabet, this.answer2)
		this.answer3 = this.alphabet[Math.floor(Math.random() * this.alphabet.length)];
		this.alphabet = this.removing(this.alphabet, this.answer3)
		
		this.answers = [this.missingletter, this.answer1, this.answer2, this.answer3]
		this.answers = this.shuffle(this.answers)
		return this.answers
	},
	methods:{
		removing: function(array, element) {
			return array.filter(el => el !== element);
		},
		shuffle: function(array) {
			var currentIndex = array.length, temporaryValue, randomIndex;

			while (0 !== currentIndex) {

				// Pick a remaining element...
				randomIndex = Math.floor(Math.random() * currentIndex);
				currentIndex -= 1;

				// And swap it with the current element.
				temporaryValue = array[currentIndex];
				array[currentIndex] = array[randomIndex];
				array[randomIndex] = temporaryValue;
			}
			return array;
		},
		nextquestions: function(){
			if (this.selected === ''){
				this.msg = 'You need to select an answer!'
			}
			else{
				if(this.missingletter === this.selected){
					this.counter++
					console.log(this.counter++)
					this.$emit('counter', this.counter++)
				}
				else{
					this.msg='wrong Answer'
				}
				this.$emit('questionorder', this.questionorder++)
			}
		}
	}
}
</script>
<style scoped>
.wording{
	font-size:50px;
}
.my_answer{
	background: #ff0;
	border: #f00 10px solid;
	font-size: 20px;
    border-radius: 30px;
    padding: 20px;
}
.check{
	height: 25px;
	width: 25px;
	margin-bottom: 16px;
}
.label{
	font-size:40px;
	width:70px;
	margin-bottom: 16px;
}
.column1{
	display:inline-block;
	text-align: left;
}
.column2{
	display:inline-block;
}
.checking{
	width:1000px;
    margin: auto;
}
.msg{
	color:red;
}
button:focus {
	outline: 0;
	box-shadow: none;
}
</style>