<template>
	<div class="fullscreen">
		<start @mouseover.native="beginsound" @click.native="startedd = !startedd; started = !started; qstart = !qstart" v-if="startedd"></start>
		<p class="through" v-if="started">{{counter}}/{{total}}</p>
		<p class="question" v-if="qstart">{{word}}</p>
		<remember v-if="qstart" @mouseover.native="beginsound" @click.native="started2 = !started2; rmember = !rmember; qstart = !qstart" ></remember>
		<qa v-if="started2" :word="word" @counter="correct" @questionorder='nextquestion'></qa>
		<div v-if="finished">
			<p>Your score is: {{counter}}/{{total}}</p>
		</div>
	</div>
</template>
<script>
import start from './start.vue'
import qa from './qa.vue'
import remember from './remember.vue'
import startsound from '@/assets/voiceCommands/letsstart.mp3'


export default {
  name: 'quiz',
	components: {
		start,
		remember,
		qa
	},
	props:[
		'words',
		'total'
	],
	data(){
		return{
			'counter':0,
			'questions': 0,
			'questionorder': 0,
			'started': false,
			'startedd': true,
			'started2': false,
			'qstart': false,
			'finished': false,
			'word': '',
			'audio': true
		}
	},
	methods:{
		beginsound: function(){
			this.audio = new Audio(startsound)
			this.audio.play()
			this.word = this.words[this.questionorder]
		},
		correct: function(){
			this.counter++
		},
		nextquestion: function(){
			this.started2 = false
			this.questionorder++
			if (this.questionorder === this.total){
				this.qstart = false
				this.finished = true
				this.started = false
				this.$emit('refresh', true)
			}
			else{
				this.word = this.words[this.questionorder]
				this.qstart = true
			}
		}
	}
}
</script>
<style scoped>
.fullscreen{
	width:100%;
	height: 100%;
	margin-top:0;
}
.through{
	float:left;
	font-size: 50px;
	position: absolute;
    margin-top: 0;
}
.question{
	font-size: 100px;
}
p{
	font-size:100px;
}
</style>