<template>
  <div id="app">
    <optional v-on:questions="noQuestions($event)" v-if="seen">
	</optional>
	<quiz :words="words" :total="total" @refresh="refreshing" v-if="seenn">
	</quiz>
	<button v-if="refresh" @click="newquiz">New Quiz</button>
  </div>
</template>

<script>
import optional from './components/optional.vue'
import json from '@/assets/json/words.json'
import quiz from './components/quiz.vue'

export default {
  name: 'app',
  components: {
	optional,
	quiz
  },
  data(){
	return{
		'questions': 0,
		json: json,
		'words': [],
		'counter': 0,
		'position': 0,
		'seen': true,
		'seenn': false,
		'total': 0,
		'refresh':false
	}
  },
  methods:{
	noQuestions:function(questioning){
		this.questions = questioning -1;
		var count = Object.keys(json).length;
		for(var x = 0; x<=this.questions; x++){
			this.position = Math.floor((Math.random() * count));
			this.words.push(this.json[this.position]["word"]);
		}
		this.$emit("words", this.words);
		this.seen = false;
		this.seenn = true;
		this.total = this.words.length;		
	},
	refreshing:function(){
		this.refresh = true
	},
	newquiz:function(){
		location.reload();
	}
  }
}
</script>

